<template>
  <div id="MessageBoxPage" style="background-color:#eee">
    <vue-nav-bar
          title="MessageBox"
          left-text=""
          left-arrow
          @click-left="goBack"
    />

    <div class="div_content">
      <div class="div_content_item">
          <div style="margin:6px;"> ($alert, alert,)</div>
          <vue-button  @click="open1"></vue-button>
      </div>

      <div class="div_content_item">
          <div style="margin:6px 10px 15px 6px;">($confirm, confirm)</div>
          <vue-button  @click="open2"></vue-button>
      </div>

      

    </div>
    
  </div>
</template>

<style scoped>
  .div_content{
    overflow: auto;
    height: calc(100vh - 46px);
  }
  .div_content_item{
    margin:6px 0 10px;
    background-color: #ffffff;
    padding:15px;
    border-radius: 4px;
  }
</style>

<script>
export default {
  name: 'MessageBoxPage',
  components: {
  },

  data() {
    return {
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    open1: function() {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const self = this;
      self.$alert({
        title: "Title",
        message: "This is a message",
        callback: function(action) {
          self.$notify({title: action});
        }
      });
    },
    open2: function() {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const self = this;
        self.$confirm({
          title: "Warning",
          message: "This will delete the file. Continue?",
          type: "warning"
        }).then(function(action) {
          self.$notify({message: action});
        }).catch(function(action) {
          self.$notify({message: action});
        });
      }
  },
};
</script>
