<template>
    <div id="ToastPage" style="background-color:#eee">
      <vue-nav-bar
          title="Notification"
          left-text=""
          left-arrow
          @click-left="goBack"
      />
      <div style="margin-top:20px;">
        <vue-row>
          <vue-button style="width:100%" plain @click="openSuccessNotify">success</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openWarningNotify">warning</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openInfoNotify">info</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openErrorNotify">error</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openTitleNotify"></vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openLoadingNotify">Loading</vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openTextNotify"></vue-button>
        </vue-row>
        <vue-row>
          <vue-button style="width:100%" plain @click="openLongTextNotify"></vue-button>
        </vue-row>
    </div>
  </div>
</template>
<style>
  .notification-demo {
    color: #fff;
    font-size: 16px;
    background-color:rgba(0,0,0,0.8);
  }
</style>

<script>
export default {
  name: 'ToastPage',
  components: {
  },

  data() {
    return {
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    openSuccessNotify() {
      this.$notify({
        title: 'Success',
        message: "I'm success notify",
        type: 'success',
        duration: 2000,
      });
    },
    openWarningNotify() {
      this.$notify({
        title: 'Warning',
        message: "I'm warning notify",
        type: 'warning',
        duration: 2000,
      });
    },
    openInfoNotify() {
      this.$notify({
        title: 'Info',
        message: 'I\'m info notify',
        type: 'info',
      });
    },
    openErrorNotify() {
      this.$notify({
        title: 'Error',
        message: "I'm error notify",
        type: 'error',
        position: 'top-right',
        duration: 0,
      });
    },
    openTitleNotify() {
      this.$notify({
        title: '',
        message: '',
        duration: 2000,
      });
    },
    openLoadingNotify() {
      this.$notify({
        message: 'Loading',
        iconClass: 'vue-icon-loading',
        duration: 2000,
      });
    },
    openTextNotify() {
      this.$notify({
        message: '',
        duration: 2000,
      });
    },
    openLongTextNotify() {
      this.$notify({
        message: '',
        customClass: 'notification-demo',
        duration: 3000,

      });
    },
  },
};
</script>
