<template>
  <div id="Demo6Page" style="background-color:#eee;overflow:hidden;">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />
    <div class="md-example-child-scroll-view-6">
      <vue-tabs
        v-model="activeName"
        @tab-click="onTabClick"
      >
        <vue-tab-pane class="content" name="scrollView0" label="Block - 1">
          <vue-scroll-view
            ref="scrollView0"
            :scrolling-x="false"
            manual-init
            @refreshing="onRefresh(0)"
          >
            <vue-scroll-view-refresh
              slot="refresh"
              slot-scope="{ scrollTop, isRefreshActive, isRefreshing }"
              :scroll-top="scrollTop"
              :is-refreshing="isRefreshing"
              :is-refresh-active="isRefreshActive"
            ></vue-scroll-view-refresh>
            <div
              v-for="i in list0"
              :key="i"
              class="scroll-view-item">{{`1 - ${i}`}}
            </div>
          </vue-scroll-view>
        </vue-tab-pane>
        <vue-tab-pane class="content" name="scrollView1" label="Block - 2">
          <vue-scroll-view
            ref="scrollView1"
            :scrolling-x="false"
            manual-init
            @refreshing="onRefresh(1)"
          >
            <vue-scroll-view-refresh
              slot="refresh"
              slot-scope="{ scrollTop, isRefreshActive, isRefreshing }"
              :scroll-top="scrollTop"
              :is-refreshing="isRefreshing"
              :is-refresh-active="isRefreshActive"
            ></vue-scroll-view-refresh>
            <div
              v-for="i in list1"
              :key="i"
              class="scroll-view-item">{{`2 - ${i}`}}
            </div>
          </vue-scroll-view>
        </vue-tab-pane>
      </vue-tabs>
    </div>
  </div>
</template>

<style>
  .md-example-child-scroll-view-6 {
    /* position: relative; */
    height:calc(100vh - 50px);
    background: #FFF;
  }
  .vue-tabs{
    height:100%;
  }

  .vue-tabs__content {
    height: calc(100% - 45px);
  }
  
  .scroll-view-item {
    width:100vw;
    height: 42px;
    padding: 10px 20px;
    font-size: 20px;
    border-bottom: .5px solid #efefef;
  }
</style>

<script>

export default {
  name: 'Demo6Page',
  components: {
  },
  data() {
    return {
      list0: 20,
      list1: 5,
      isFinished: false,
      activeName: 'scrollView0',
    }
  },
  mounted() {
    //
    this.$refs['scrollView0'].init();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    onRefresh(index) {
      // async data
      setTimeout(() => {
        this[`list${index}`] += 5
        this.$refs[`scrollView${index}`].finishRefresh()
      }, 2000);
    },
    onTabClick(tab) {
      console.log(tab.name);
      //
      this.$refs[tab.name].init();
    },
  },
}

</script>

