<template>
  <div id="Demo4Page" style="background-color:#eee">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />
    <div class="md-example-child-scroll-view-4">
      <vue-scroll-view
        ref="scrollView"
        :scrolling-x="false"
        @scroll="onScroll"
      >
        <div
          v-for="i in category"
          :key="i"
          class="scroll-view-category"
        >
          <p class="scroll-view-category-title">{{ i }}</p>
          <div
            v-for="j in list"
            :key="j"
            class="scroll-view-item"
          >
            {{`${i} - ${j}`}}
          </div>
        </div>
      </vue-scroll-view>
      <p v-if="activeBlockIndex > 0" class="scroll-view-striky-title">{{ activeBlockIndex }}</p>
    </div>
  </div>
</template>

<style>
.md-example-child-scroll-view-4 {
  position: relative;
  height: calc(100vh - 46px);
  background: #FFF;
}

.scroll-view-striky-title {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}
.scroll-view-category-title, .scroll-view-striky-title {
  margin:0;
  padding: 10px 0;
  text-align: center;
  font-size: 25px;
  background-color: rgba(245,245,245);
}
.scroll-view-item {
    width:100vw;
    height: 42px;
    padding: 10px 20px;
    font-size: 20px;
    border-bottom: .5px solid #efefef;
  }

</style>

<script>

export default {
  name: 'Demo4Page',
  components: {
  },
  data() {
    return {
      category: 20,
      list: 5,
      dimensions: [],
      scrollY: 0,
    }
  },
  computed: {
    activeBlockIndex() {
      let activeIndex = -1
      this.dimensions.forEach((dimension, index) => {
        if (this.scrollY >= dimension[0] && this.scrollY <= dimension[1]) {
          activeIndex = index + 1
        }
      })
      return activeIndex
    },
  },
  mounted() {
    // blockscroller
    this.initScrollBlock()
    // this.$refs.scrollView.reflowScroller()
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    initScrollBlock() {
      const blocks = this.$el.querySelectorAll('.scroll-view-category')
      let offset = 0
      Array.prototype.slice.call(blocks).forEach((block, index) => {
        const innerHeight = block.clientHeight
        this.$set(this.dimensions, index, [offset, offset + innerHeight])
        offset += innerHeight
      })
    },
    onScroll({scrollTop}) {
      this.scrollY = scrollTop
    },
  },
}

</script>

