<template>
  <div id="TabsPage" style="background-color:#eee;">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    >
      <template #right>
          <vue-text-icon iconName="plus" iconColor="#409eff" iconSize="8" label="Add Tab" labelColor="#409eff" labelSize="8"  @click="addTab()" />
      </template>
    </vue-nav-bar>
    <div class="div_content">
      <vue-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab">
        <vue-tab-pane v-for="(item, index) in editableTabs" :key="index" :label="item.title" :name="item.name">
          <div class="div_content_item">
            <div style="margin:6px;">   {{item.content}}  </div>
          </div>
        </vue-tab-pane>
      </vue-tabs>

 
      


    </div>
  </div>
</template>

<style scoped>
  .div_content{
    height: calc(100vh - 46px);
  }
  .div_content_item{
    margin:10px 0;
    background-color: #ffffff;
    padding:15px 6px;
    border-radius: 4px;
  }
</style>

<script>
export default {
  name: 'TabsPage',
  components: {
  },

  data() {
    return {
        editableTabsValue: "2",
        editableTabs: [{
          title: "Tab 1",
          name: "1",
          content: "Tab 1 content"
        }, {
          title: "Tab 2",
          name: "2",
          content: "Tab 2 content"
        }],
        tabIndex: 2
    };
  },
  methods: {
    goBack() {
        this.$router.go(-1);
    },
    addTab() {
        const newTabName = ++this.tabIndex + "";
        this.editableTabs.push({
          title: "Tab " + newTabName,
          name: newTabName,
          content: "Tab " + newTabName + " content"
        });
        this.editableTabsValue = newTabName;
      },
      removeTab(targetName) {
        const tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetName) {
          tabs.forEach(function(tab, index) {
            if (tab.name === targetName) {
              const nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(function(tab) {
          return tab.name !== targetName
        });
      }
  },
};
</script>
