    goBack() {
      this.$router.go(-1);
    },
    onItemClick(i) {
      const currItem = document.getElementsByClassName('vue-list-item')[i-1];
      const lastActiveItem = document.getElementsByClassName('is-active')[0];
      if(lastActiveItem)
      lastActiveItem.classList.remove('is-active');
      currItem.classList.add('is-active');
      this.$notify({message: `Click ${i}`});
    },
    onScroll({scrollLeft, scrollTop}) {
      console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
