<template>
  <div id="Demo2Page" style="background-color:#eee">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />
    <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-2">
      <vue-scroll-view
        ref="scrollView"
        :scrolling-x="false"
        @refreshing="onRefresh"
      >
        <vue-scroll-view-refresh
          slot="refresh"
          slot-scope="{ scrollTop, isRefreshActive, isRefreshing }"
          :scroll-top="scrollTop"
          :is-refreshing="isRefreshing"
          :is-refresh-active="isRefreshActive"
        >
        </vue-scroll-view-refresh>
        <div
          v-for="i in list"
          class="scroll-view-item"
          :key="i"
          @click="onItemClick(i)"
        >
          {{i}}
        </div>
      </vue-scroll-view>
    </div>
  </div>
</template>

<style>
  .md-example-child-scroll-view-2 {
    height: calc(100vh - 50px);
    background: #FFF;
  }

  .scroll-view-item {
    width:100vw;
    height: 42px;
    padding: 10px 20px;
    font-size: 20px;
    border-bottom: .5px solid #efefef;
  }
</style>

<script>

export default {
  name: 'Demo2Page',
  components: {
  },
  data() {
    return {
      list: 100,
    }
  },
  mounted() {
    // window.ScrollViewTrigger1 = () => {
    //   this.$refs.scrollView.triggerRefresh()
    // }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    onRefresh() {
      // async data
      setTimeout(() => {
        this.list += 5
        this.$refs.scrollView.finishRefresh();
      }, 2000)
    },
    onItemClick(i) {
      this.$notify({message: `Click ${i}`});
    },
  },
}

</script>

