<template>
  <div id="UploadPage" style="background-color:#eee">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />

    <div class="div_content">
      <div class="div_content_item">
          <div style="margin:6px;"> </div>
          <vue-upload :auto-upload="false" :on-preview="handlePreview" :on-remove="handleRemove" :before-upload="beforeAvatarUploadTXT">
            <vue-button size="small" type="primary">Click to upload</vue-button>
            <div slot="tip" class="vue-upload__tip">Text files with a size less than than 2MB</div>
          </vue-upload>
      </div>

      <div class="div_content_item">
          <div style="margin:6px 10px 15px 6px;"> list-typepicture-card</div>
          <vue-upload multiple :max="6" :auto-upload="false" list-type="picture-card" :on-preview="handlePictureCardPreview" :before-upload="beforeAvatarUploadImg">
            <i class="vue-icon-plus"></i>
          </vue-upload>
          <vue-image-viewer  v-if="showViewer" :on-close="closeViewer" :url-list="imgList" :active-index="activeImgIndex"/>
      </div>

      <div class="div_content_item">
          <div style="margin:6px;"> list-typepicture</div>
          <vue-upload multiple :max="9" :auto-upload="false" list-type="picture" :on-preview="handlePictureCardPreview" :on-remove="handleRemove" :before-upload="beforeAvatarUploadImg">
            <vue-button size="small" type="primary">Click to upload</vue-button>
            <div slot="tip" class="vue-upload__tip">JPG files with a size less than than 2MB</div>
          </vue-upload>
      </div>

      <div class="div_content_item">
          <div style="margin:6px;"> on-change</div>
          <vue-upload :auto-upload="false" :on-change="handleChange" :file-list="fileList">
            <vue-button size="small" type="primary">Click to upload</vue-button>
          </vue-upload>
      </div>

      <div class="div_content_item">
          <div style="margin:6px;"> drag</div>
          <vue-upload :auto-upload="false" drag multiple>
            <i class="vue-icon-upload"></i>
            <div class="vue-upload__text">Drop file here or <em>click to upload</em></div>
          </vue-upload>
      </div>

      <div class="div_content_item">
          <div style="margin:6px;"> </div>
          <vue-upload ref="upload" :auto-upload="false">
            <vue-button slot="trigger" size="small" type="primary">select file</vue-button>
            <vue-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">upload to server</vue-button>
          </vue-upload>
      </div>

    </div>
    
  </div>
</template>

<style scoped>
  .div_content{
    overflow: auto;
    height: calc(100vh - 46px);
  }
  .div_content_item{
    margin:6px 0 10px;
    background-color: #ffffff;
    padding:15px;
    border-radius: 4px;
  }
</style>

<script>
export default {
  name: 'UploadPage',
  components: {
  },

  data() {
    return {
      imgList: [],
      activeImgIndex:0,
      showViewer: false,
      fileList:[],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    handleRemove: function(file) {
      this.$notify({message: 'Removed File: ' + file.name});
    },
    handlePreview: function(file) {
      this.$notify({title: 'Select File: ' + file.name});
    },
    beforeAvatarUploadTXT: function(file) {
      const isTXT = file.type === "text/plain";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isTXT) {
        this.$notify({
          title: 'Error',
          message: "Must be a text file",
          type: 'error',
          duration: 3000,
        });
      }
      if (!isLt2M) {
        this.$notify({
          title: 'Error',
          message: "File with a size less than 2MB",
          type: 'error',
          duration: 3000,
        });
      }
      return isTXT && isLt2M;
    },
    handlePictureCardPreview: function(file, index, fileList) {
      this.activeImgIndex = index;
      this.imgList = [];
      for(let i=0; i<fileList.length;i++){
        const imgInfo = fileList[i];
        this.imgList.push(imgInfo.url);
      }
      this.showViewer = true;
    },
    beforeAvatarUploadImg: function(file) {
      const isJPG = file.type === "image/jpeg";
      const isGIF = file.type === 'image/gif';
      const isPNG = file.type === 'image/png';
      const isBMP = file.type === 'image/bmp';
      if (!isJPG && !isGIF && !isPNG && !isBMP) {
        this.$notify({
          title: 'Error',
          message: "Must be a JPG file",
          type: 'error',
          duration: 3000,
        });
      }
      return isJPG || isGIF || isPNG || isBMP;
    },
    closeViewer: function() {
        this.showViewer = false;
    },
    handleChange: function(file, fileList) {
      if(fileList.length>1){
        fileList.splice(0,1);
        this.$notify({message: ''});
      }
      this.fileList = fileList;
      
    },
    submitUpload: function() {
        this.$refs.upload.submit();
      }
  },
};
</script>
