<template>
  <div>
    <div v-if="!isRange">
      <vue-input
        :value="fieldValue"
        :readonly="true"
        :disabled="disabled"
        :clickable="true"
        :clearable="clearable"
        :label="label"
        :size="size"
        :autofocus="autofocus"
        :placeholder="placeholder"
        :text-align="textAlign"
        @click.native="onClick"
        :icon="showClose ? '' + clearIcon : ''"
        :on-icon-click="handleClickIcon"
      >
        <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
      </vue-input>
      <popup v-model="showPicker" position="bottom">
        <time-picker
          :formatter="formatter"
          position="bottom"
          :class="cls"
          :title="title"
          ref="picker"
          :value="value"
          :type="type"
          :min-hour="mMinHour"
          :max-hour="mMaxHour"
          :min-minute="mMinMinute"
          :max-minute="mMaxMinute"
          :min-second="mMinSecond"
          :max-second="mMaxSecond"
          :loading="loading"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="showToolbar"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="onConfirm"
          @cancel="onCancel"
          @change="onChange"
        ></time-picker>
      </popup>
    </div>
    <div v-else style="background-color: #fff;">
      <vue-input
        style="width:48%"
        :value="fieldValue"
        :readonly="true"
        :disabled="disabled"
        :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
        :clickable="true"
        :clearable="clearable"
        :label="label"
        :size="size"
        :autofocus="autofocus"
        :text-align="textAlignRange"
        @click.native="onClick"
      >
        <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
      </vue-input>
      <span>{{rangeSeparator}}</span>
      <vue-input
        style="width:48%"
        :value="endFieldValue"
        :readonly="true"
        :disabled="disabled"
        :clickable="true"
        :clearable="clearable"
        :label="label"
        :size="size"
        :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
        :autofocus="autofocus"
        :text-align="textAlignRange"
        @click.native="endOnClick"
        :icon="showClose ? '' + clearIcon : ''"
        :on-icon-click="handleClickIcon"
      ></vue-input>

      <popup v-model="showPicker" position="bottom">
        <time-picker
          :formatter="formatter"
          position="bottom"
          :class="cls"
          :title="startTitle || $t('vue.datepicker.startTime')"
          :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
          ref="picker"
          :value="startValue"
          :type="type"
          :min-hour="mMinHour"
          :max-hour="mMaxHour"
          :min-minute="mMinMinute"
          :max-minute="mMaxMinute"
          :min-second="mMinSecond"
          :max-second="mMaxSecond"
          :loading="loading"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="showToolbar"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="startOnConfirm"
          @cancel="startOnCancel"
          @change="startOnChange"
        ></time-picker>
      </popup>
      <popup v-model="endShowPicker" position="bottom">
        <time-picker
          :formatter="formatter"
          position="bottom"
          :class="cls"
          :title="endTitle || $t('vue.datepicker.endTime')"
          :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
          ref="endPicker"
          :value="endValue"
          :type="type"
          :min-hour="mMinHour"
          :max-hour="mMaxHour"
          :min-minute="mMinMinute"
          :max-minute="mMaxMinute"
          :min-second="mMinSecond"
          :max-second="mMaxSecond"
          :loading="loading"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="showToolbar"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="endOnConfirm"
          @cancel="endOnCancel"
          @change="endOnChange"
        ></time-picker>
      </popup>
    </div>
  </div>
</template>

<script>
import Popup from '../popup';
import TimePicker from '../datetime-picker';


export default {
  name: 'VueTimePickerMobile',
  components: { TimePicker, Popup },
  props: {
    cls: {
      type: String,
    },
    confirmButtonText: {
      type: String,
      default: '',
    },
    cancelButtonText: {
      type: String,
      default: '',
    },
    showToolbar: {
      type: Boolean,
      default: true,
    },
    title: {
      type: String,
      default:''
    },
    startTitle: {
      type: String,
      default:''
    },
    endTitle: {
      type: String,
      default:''
    },
    loading: {
      type: Boolean,
      default: false,
    },
    itemHeight: {
      type: [String, Number],
    },
    visibleItemCount: { // 
      type: Number,
      default: 5,
    },
    swipeDuration: { // ms
      type: [String, Number],
      default: 1000,
    },
    label: {
      type: String,
    },
    value: {
      type: [String,Date,Array],
    },
    textAlign: {
      type: String,
    },
    textAlignRange: {
      type: String,
      default:'center'
    },
    size: {
      type: String,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
    },
    startPlaceholder: {
      type: String,
      default:''
    },
    endPlaceholder: {
      type: String,
      default:''
    },
    rangeSeparator: {//
      type: String,
      default:'-'
    },
    type: {
      type: String,
      default: 'time',
    },
    minHour: {
      type: [Number,String]
    },
    maxHour: {
      type: [Number,String]
    },
    minMinute: {
      type: [Number,String],
    },
    maxMinute: {
      type: [Number,String],
    },
    minSecond: {
      type: [Number,String],
    },
    maxSecond: {
      type: [Number,String],
    },
    valueFormat: {
      type: String,
    },
    prefixIcon: {
      type: String,
    },
    formatter: {
      type: Function,
    },
    isRange: {
      type: Boolean,
      default:false
    },
    clearIcon: {
        type: String,
        default: 'vue-icon-close'
    },
    
  },
  mounted() {
    //this.showPicker = false;
    //this.endShowPicker = false;
  },
  data() {
    return {
      format:'HH:mm:ss',
      showPicker: false,
      endShowPicker:false,
      fieldValue: '',
      endFieldValue:'',
      startValue:'',
      endValue:'',
      mMinHour:this.minHour,
      mMaxHour:this.maxHour,
      mMinMinute:this.minMinute,
      mMaxMinute:this.maxMinute,
      mMinSecond:this.minSecond,
      mMaxSecond:this.maxSecond,
    };
  },
  methods: {
    handleClickIcon: function (event) {
        this.fieldValue = '';
        this.endFieldValue = '';
        this.startValue=null;
        this.endValue=null;
        event.stopPropagation();
        this.$emit('input', null);
    },
    onClick() {
      if(this.isRange){
        this.mMinHour   = this.minHour;
        this.mMinMinute = this.minMinute;
        this.mMinSecond = this.minSecond;

        if(this.endFieldValue){
          const time = VueUtil.parseDate(this.endFieldValue,'HH:mm:ss');
          this.mMaxHour   = time.getHours();
          this.mMaxMinute = time.getMinutes();
          this.mMaxSecond = time.getSeconds();
        }else{
          this.mMaxHour   = this.maxHour;
          this.mMaxMinute = this.maxMinute;
          this.mMaxSecond = this.maxSecond;
        }
      }
      this.showPicker = true;
    },
    onConfirm(value, index) {
      this.showPicker = false;
      this.$emit('input', value);

     // const dt = new Date();
      //this.$emit('input', new Date( `${dt.getFullYear()}-${dt.getMonth()}-${dt.getDay()} ${value}`));

      this.$emit('confirm', value, index);
    },
    onCancel(value, index) {
      this.showPicker = false;
      this.$emit('cancel', value, index);
    },
    onChange(picker, value, index) {
      this.$emit('change', picker, value, index);
    },
    endOnClick() {
      if(this.fieldValue){
        const time = VueUtil.parseDate(this.fieldValue,'HH:mm:ss');
        this.mMinHour   = time.getHours();
        this.mMinMinute = time.getMinutes();
        this.mMinSecond = time.getSeconds();
      }else{
        this.mMinHour   = this.minHour;
        this.mMinMinute = this.minMinute;
        this.mMinSecond = this.minSecond;
      }
      this.mMaxHour   = this.maxHour;
      this.mMaxMinute = this.maxMinute;
      this.mMaxSecond = this.maxSecond;
      this.endShowPicker = true;
    },
    startOnConfirm(value, index) {
      if(this.endValue&&value>this.endValue){
        console.error('The start time cannot be later than the end time!');
        return;
      }
      this.showPicker = false;
      if(this.valueFormat){
         if(VueUtil.isDate(value)){
            this.$emit('input', [ VueUtil.formatDate(value, this.valueFormat),this.endValue]);
          }else{
            this.$emit('input', [value,this.endValue]);
          }
      }else{
         this.$emit('input', [value,this.endValue]);
      }
     
      this.$emit('start-confirm', value, index);
    },
    startOnCancel(value, index) {
      this.showPicker = false;
      this.$emit('start-cancel', value, index);
    },
    startOnChange(picker, value, index) {
      this.$emit('start-change', picker, value, index);
    },
    endOnConfirm(value, index) {

      if(this.startValue&&value<this.startValue){
        console.error('The end time cannot be earlier than the start time!');
        return;
      }
      this.endShowPicker = false;
     // this.$emit('input', [this.startValue,value]);

      if(this.valueFormat){
         if(VueUtil.isDate(value)){
            this.$emit('input', [this.startValue, VueUtil.formatDate(value, this.valueFormat)]);
          }else{
            this.$emit('input', [this.startValue,value]);
          }
      }else{
        this.$emit('input', [this.startValue,value]);
      }

      this.$emit('end-confirm', value, index);
    },
    endOnCancel(value, index) {
      this.endShowPicker = false;
      this.$emit('end-cancel', value, index);
    },
    endOnChange(picker, value, index) {
      this.$emit('end-change', picker, value, index);
    },

  },
  watch: {
    value(val) {
      if(VueUtil.isArray(val)){
         if(VueUtil.isDate(val[0])){
            this.fieldValue = VueUtil.formatDate(val[0], this.format);
         }else{
            this.fieldValue = val[0];
         }
         this.startValue = val[0];

        if(VueUtil.isDate(val[1])){
            this.endFieldValue = VueUtil.formatDate(val[1], this.format);
         }else{
            this.endFieldValue = val[1];
         }
        this.endValue = val[1];
      }else{
        if(VueUtil.isDate(val)){
            this.fieldValue = VueUtil.formatDate(val, this.format);
         }else{
            this.fieldValue = val;
         }
        
      }
      
    },
  },
  computed: {
    showClose(){
      if(VueUtil.isArray(this.value)){
        return this.value && this.value.length>0;
      }
      return this.value?true:false;
    },
    triggerClass() {
      return this.prefixIcon || (this.type.indexOf('time') !== -1 ? 'vue-icon-time' : 'vue-icon-date');
    },
  },
};
</script>
