<template>
  <div id="HomePage" style="background-color:#eee;">
    <vue-nav-bar title="" />
    <div style="height: calc(100vh - 100px);overflow-y:auto;">
      <vue-row style="margin:4px 20px;" v-for="item in mPageList" :key="item.path">
        <vue-button style="width:100%;text-align:left;padding:12px 20px;" @click="openPage(item.path)">{{item.labelName}}</vue-button>
      </vue-row>
    </div>
    <vue-tabbar v-model="activeIndex" :fixed="true" @change="onChange">
          <vue-tabbar-item v-for="item in mItemList" :key="item.iconName"  :icon="item.iconName">{{item.tabLabel}}</vue-tabbar-item>
      </vue-tabbar>
  </div>
</template>
<style>
  .vue-nav-bar__title{
    max-width: 60%;
    margin: 0 auto;
    color:#1989fa;
    font-weight: 500;
    font-size: 16px;
  }
</style>

<script>
export default {
  name: 'HomePage',
  components: {
  },

  data() {
    return {
      mPageList: [
        { labelName: 'ScrollView', path: '/scroll-view' },
        { labelName: 'ActionSheet', path: '/action-sheet' },
        { labelName: 'NavBar', path: '/nav-bar' },
        { labelName: 'TabBar', path: '/tab-bar' },
        { labelName: 'Grid', path: '/grid' },
        // { labelName: '', path: '/icon' },
        { labelName: 'SwipeCell', path: '/swipe-cell' },
        { labelName: 'TextIcon', path: '/text-icon' },
        { labelName: 'Stepper', path: '/stepper' },
        { labelName: 'IndexBar', path: '/index-bar' },
        { labelName: 'Skeleton', path: '/skeleton' },
        { labelName: 'Cascader', path: '/cascader' },
        { labelName: 'DatetimePicker', path: '/datetime-picker' },
        { labelName: 'Select', path: '/select' },
        { labelName: 'Notify', path: '/notification' },
        { labelName: 'Input', path: '/input' },
        { labelName: 'Image', path: '/image' },
        { labelName: 'Upload', path: '/upload' },
        { labelName: 'Tabs', path: '/tabs-demos' },
        { labelName: 'AdaptiveGroup', path: '/adaptive-group' },
        { labelName: 'MessageBox', path: '/message-box' },
        { labelName: 'Dialog', path: '/dialog' },
        { labelName: 'ColorPicker', path: '/color-picker' },
        { labelName: 'Form', path: '/form' },
        { labelName: 'Collapse', path: '/collapse' },
        // { labelName: '', path: '/table-demos' },
      ],
      activeIndex:0,
      mItemList:[
        {
          iconName: 'home',
          tabLabel: ''
        },
        {
          iconName: 'search',
          tabLabel: ''
        },
        {
          iconName: 'user',
          tabLabel: ''
        },
        {
          iconName: 'setting',
          tabLabel: ''
        },
      ],
    };
  },
  methods: {
    openPage(path) {
      this.$router.push(path);
    },
    onChange(index) {
      if(index>0){
        this.$notify({message: ''});
      }
    },
  },
};
</script>
