<template>
  <div>
    <div v-if="!isRange">
      <vue-input
        :value="fieldValue"
        :readonly="true"
        :disabled="disabled"
        :clickable="true"
        :clearable="clearable"
        :size="size"
        :placeholder="placeholder"
        :text-align="textAlign"
        :icon="showClose ? '' + clearIcon : ''"
        @click.native="onClick"
        :on-icon-click="handleClickIcon"
      >
        <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
      </vue-input>
      <popup v-model="showPicker" position="bottom">
        <datetime-picker
          :formatter="actualFormatter"
          position="bottom"
          :class="popperClass"
          :title="title"
          ref="picker"
          :value="value"
          :type="type"
          :min-date="mMinDate"
          :max-date="mMaxDate"
          :loading="loading"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="showToolbar"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          :first-day-of-week="firstDayOfWeek"
          @confirm="onConfirm"
          @cancel="onCancel"
          @change="onChange"
        >
        </datetime-picker>
      </popup>
    </div>
    <div v-else style="background-color: #fff;">
          <vue-input style="width:48%"
            :value="fieldValue"
            :readonly="true"
            :disabled="disabled"
            :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
            :clickable="true"
            :clearable="clearable"
            :size="size"
            :text-align="textAlignRange"
            @click.native="onClick"
          >
            <i slot="prefix" :class="['vue-input__icon',triggerClass]"></i>
          </vue-input>
         <span >{{rangeSeparator}}</span>
            <vue-input style="width:48%"
                :value="endFieldValue"
                :readonly="true"
                :disabled="disabled"
                :clickable="true"
                :clearable="clearable"
                :size="size"
                :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
                :text-align="textAlignRange"
                @click.native="endOnClick"
                :icon="showClose ? '' + clearIcon : ''"
                :on-icon-click="handleClickIcon"
              >
            </vue-input>

        <popup v-model="showPicker" position="bottom">
        <datetime-picker
          :formatter="actualFormatter"
          position="bottom"
          :class="popperClass"
          :title="startTitle || $t('vue.datepicker.startDate')"
          :placeholder="startPlaceholder || $t('vue.datepicker.startPlaceholder')"
          ref="picker"
          :value="startValue"
          :type="type"
          :min-date="mMinDate"
          :max-date="mMaxDate"
          :loading="loading"
          :confirm-button-text="confirmButtonText"
          :cancel-button-text="cancelButtonText"
          :show-toolbar="showToolbar"
          :item-height="itemHeight"
          :visible-item-count="visibleItemCount"
          :swipe-duration="swipeDuration"
          @confirm="startOnConfirm"
          @cancel="startOnCancel"
          @change="startOnChange"
        >
        </datetime-picker>
      </popup>    
        <popup v-model="endShowPicker" position="bottom">
          <datetime-picker
            :formatter="actualFormatter"
            position="bottom"
            :class="popperClass"
            :title="endTitle || $t('vue.datepicker.endDate')"
            :placeholder="endPlaceholder || $t('vue.datepicker.endPlaceholder')"
            ref="endPicker"
            :value="endValue"
            :type="type"
            :min-date="mMinDate"
            :max-date="mMaxDate"
            :loading="loading"
            :confirm-button-text="confirmButtonText"
            :cancel-button-text="cancelButtonText"
            :show-toolbar="showToolbar"
            :item-height="itemHeight"
            :visible-item-count="visibleItemCount"
            :swipe-duration="swipeDuration"
            @confirm="endOnConfirm"
            @cancel="endOnCancel"
            @change="endOnChange"
          >
          </datetime-picker>
        </popup>

        </div>

  </div>
    
  
</template>

<script>
import Popup from '../popup';
import DatetimePicker from '../datetime-picker';


export default {
  name: 'VueDatePickerMobile',
  components: { DatetimePicker, Popup },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    size: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    startPlaceholder: {
      type: String,
      default:''
    },
    endPlaceholder: {
      type: String,
      default:''
    },
    type: {
      type: String,
      default: 'date',
    },
    format: {
      type: String,
    },
    popperClass: {
      type: String,
    },
    rangeSeparator: {//
      type: String,
      default:'-'
    },
    valueFormat: {
      type: String,
    },
    value: {//(v-model)
      type: [String,Date,Array],
    },
    textAlign: {//(type 'daterange','datetimerange')
      type: String,
    },
    textAlignRange: {//(type 'daterange''datetimerange')
      type: String,
      default:'center'
    },
    minDate: {//
      type: Date,
    },
    maxDate: {//
      type: Date,
    },
    prefixIcon: {//
      type: String,
    },
    clearIcon: {//
        type: String,
        default: 'vue-icon-close'
    },
    firstDayOfWeek:{//,: 1~7
      type:Number,
      default:7,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default:''
    },
    startTitle: {
      type: String,
      default:''
    },
    endTitle: {
      type: String,
      default:''
    },
    confirmButtonText: {
      type: String,
      default: '',
    },
    cancelButtonText: {
      type: String,
      default: '',
    },
    showToolbar: {
      type: Boolean,
      default: true,
    },
    itemHeight: {
      type: [String, Number],
    },
    visibleItemCount: { // 
      type: Number,
      default: 5,
    },
    swipeDuration: { // ms
      type: [String, Number],
      default: 1000,
    },
    formatter: {
      type: Function,
    },
  },
  mounted() {
    //this.showPicker = false;
    //this.endShowPicker = false;
  },
  data() {
    return {
      showPicker: false,
      endShowPicker:false,
      fieldValue: '',
      endFieldValue:'',
      startValue:'',
      endValue:'',
      mMinDate:this.minDate,
      mMaxDate:this.maxDate
    };
  },
  methods: {
      handleClickIcon: function (event) {
        this.fieldValue = '';
        this.endFieldValue = '';
        this.startValue=null;
        this.endValue=null;
        event.stopPropagation();
        this.$emit('input', null);
      },
     actualFormatter(type, val) {
      
       if(this.formatter){
         return this.formatter(type,val);
       }

      if (type === 'week') {
        return this.$t('vue.datepicker.weekNumber', {number: val});
      } 
      return val;
    }, 
    onClick() {
      if(!this.disabled){
        this.mMinDate = this.minDate;
        if(this.endFieldValue)
          this.mMaxDate = VueUtil.toDate(this.endFieldValue);
        else
          this.mMaxDate = this.maxDate;
        this.showPicker = true;
      }
      
    },
    onConfirm(value, index) {
      
      this.showPicker = false;
      this.$emit('input', value);
      this.$emit('confirm', value, index);
    },
    onCancel(value, index) {
      this.showPicker = false;
      this.$emit('cancel', value, index);
    },
    onChange(picker, value, index) {
      this.$emit('change', picker, value, index);
    },
    endOnClick() {
      if(!this.disabled){
        if(this.fieldValue)
          this.mMinDate = VueUtil.toDate(this.fieldValue);
        else
          this.mMinDate = this.minDate;
        this.mMaxDate = this.maxDate;
        this.endShowPicker = true;
      }
      
    },
    startOnConfirm(value, index) {

      if(this.endValue&&new Date(value).getTime()>new Date(this.endValue).getTime()){
        console.error('The start date cannot be later than the end date!');
        return;
      }
      
      this.showPicker = false;
      if(this.valueFormat){
         if(VueUtil.isDate(value)){
            this.$emit('input', [ VueUtil.formatDate(value, this.valueFormat),this.endValue]);
          }else{
            this.$emit('input', [value,this.endValue]);
          }
      }else{
         this.$emit('input', [value,this.endValue]);
      }
     
      this.$emit('start-confirm', value, index);
    },
    startOnCancel(value, index) {
      this.showPicker = false;
      this.$emit('start-cancel', value, index);
    },
    startOnChange(picker, value, index) {
      this.$emit('start-change', picker, value, index);
    },
    endOnConfirm(value, index) {

      const endDate = VueUtil.formatDate(new Date(value), this.actualFormat);
      const startDate = VueUtil.formatDate(new Date(this.startValue), this.actualFormat);
      if(this.startValue&&endDate<startDate){
        console.error('The end date cannot be earlier than the start date!');
        return;
      }

      this.endShowPicker = false;
     // this.$emit('input', [this.startValue,value]);

      if(this.valueFormat){
         if(VueUtil.isDate(value)){
            this.$emit('input', [this.startValue, VueUtil.formatDate(value, this.valueFormat)]);
          }else{
            this.$emit('input', [this.startValue,value]);
          }
      }else{
        this.$emit('input', [this.startValue,value]);
      }

      this.$emit('end-confirm', value, index);
    },
    endOnCancel(value, index) {
      this.endShowPicker = false;
      this.$emit('end-cancel', value, index);
    },
    endOnChange(picker, value, index) {
      this.$emit('end-change', picker, value, index);
    },

  },
    watch: {
    value: {
      handler: function(val) {
        
        if(VueUtil.isArray(val)){
          if(VueUtil.isDate(val[0])){
              this.fieldValue = VueUtil.formatDate(val[0], this.actualFormat);
          }else{
              this.fieldValue = val[0];
          }
          this.startValue = val[0];

          if(VueUtil.isDate(val[1])){
              this.endFieldValue = VueUtil.formatDate(val[1], this.actualFormat);
          }else{
              this.endFieldValue = val[1];
          }
          this.endValue = val[1];
        }else{
          let str = VueUtil.formatDate(val, this.actualFormat);
          if(this.type==='week'){
              const week = this.$refs.picker.$refs.root.getWeekByDate(str);
              const timestr = Date.parse(str);
              const date = new Date(timestr);
              const month = date.getMonth() + 1;
              const year = date.getFullYear();
              const time = this.$t('vue.datepicker.weekFieldValue', {year, month, week});
              str = time;
          }
  
          this.fieldValue =str;
        }     
      },
      immediate: true
    },
  },
  computed: {
    showClose(){
      if(VueUtil.isArray(this.value)){
        return this.value && this.value.length>0;
      }
      return this.value?true:false;
    },
    isRange(){
      if(this.type==='daterange'||this.type==='datetimerange'){
        return true;
      }
      return false;
    },
    actualFormat(){

      if(this.type==='week'){
        return 'yyyy-MM-dd';
      }
      if(this.format){
        return this.format;
      }
      let fmt = 'yyyy-MM-dd';
      if(this.type==='year'){
         fmt = 'yyyy';
      }else if(this.type==='month'){
         fmt = 'yyyy-MM';
      }else if(this.type==='date'||this.type==='daterange'){
         fmt = 'yyyy-MM-dd';
      }else if(this.type==='datetime'||this.type==='datetimerange'){
         fmt = 'yyyy-MM-dd HH:mm:ss';
      }else{
        console.error('type of "'+this.type+'" is invalid!');
      }
      return fmt;
    },
    triggerClass() {
      return this.prefixIcon || (this.type.indexOf('time') !== -1 ? 'vue-icon-time' : 'vue-icon-date');
    },
  },
};
</script>
