      inputValue5: '',
      inputValue6: '',
      inputValue7: '',
      inputValue8: '',

      inputValue10: '',
      inputValue11: '',
      inputValue12: '',
      inputValue13: '',
      inputValue14: '',
      inputValue15: '',
      hinitMsg: '',
      customInputData1: {
        numeral:true
      },
      customInputData2: {
        numericOnly:true
      },
      customInputData3: {
        delimiter: '-',//
        blocks: [3, 3, 3],
        uppercase: true,
      },
      hinitMsg2: '',
      state1: '',
      state2: '',
      restaurants: [
        { value: 'first' },
        { value: 'second' },
        { value: 'first and second' },
      ],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    clickIconEvent() {
      this.$notify({ message: '' });
    },
    querySearch(queryString, cb) {
      const { restaurants } = this;
      const results = queryString
        ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => restaurant.value.indexOf(queryString.toLowerCase()) === 0;
    },
    handleSelect(item) {
      this.$notify({ title: item.value });
    },
  },
};
</script>
