<template>
  <div id="IndexBarPage" style="background-color:#eee;height:100%;overflow:hidden;">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />

    <vue-form ref="form" :model="form" label-width="180px" :label-responsive="false" class="div_form" labelPosition="left">
        <vue-index-bar>
          <div v-for="item in indexList" :key="item.index">
            <vue-index-anchor :index="item.index" />
            <vue-form-item v-for="child in item.child" :key="child" :label="child"/>
          </div>
        </vue-index-bar>

    </vue-form>
    
  </div>
</template>

<style scoped>
  .div_form{
    overflow: auto;
    height: calc(100vh - 60px);
    margin-top:15px;
    padding:4px;
  }
</style>

<script>
export default {
  name: 'IndexBarPage',
  components: {
  },

  data() {
    const indexList = [];
    
    const charCodeOfA = 'A'.charCodeAt(0);
    for (let i = 0; i < 26; i++) {
      const alphabet = String.fromCharCode(charCodeOfA + i);
      const item = {index:alphabet}
      const mTextList = [];
      for (let j = 0; j < 3; j++) {
        const key = j+1;
        const text = alphabet+''+key;
        mTextList.push(text);
      }
      item.child = mTextList;
      indexList.push(item);
    }

    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
      },
      indexList,
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
  },
};
</script>
