<template>
  <div id="Demo3Page" style="background-color:#eee">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />
    <div class="md-example-child-scroll-view-3">
      <vue-scroll-view
        ref="scrollView"
        :scrolling-x="false"
        @end-reached="onEndReached"
      >

      <div
          v-for="i in list"
          class="scroll-view-item"
          :key="i"
          @click="onItemClick(i)"
      >
          {{i}}
      </div>  
        <vue-scroll-view-more
          slot="more"
          :is-finished="isFinished"
        >
        </vue-scroll-view-more>
      </vue-scroll-view>
    </div>
  </div>
</template>

<style>
.md-example-child-scroll-view-3 {
  height: calc(100vh - 46px);
  background: #FFF;
}

.scroll-view-item {
    width:100vw;
    height: 42px;
    padding: 10px 20px;
    font-size: 20px;
    border-bottom: .5px solid #efefef;
  }
</style>

<script>

export default {
  name: 'Demo3Page',
  components: {
  },
  data() {
    return {
      list: 20,
      isFinished: false,
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    onEndReached() {
      if (this.isFinished) {
        return;
      }
      // async data
      setTimeout(() => {
        this.list += 5;
        if (this.list >= 50) {
        this.isFinished = true;
      }
        this.$refs.scrollView.finishLoadMore();
      }, 1000);
      
      
      
    },
    onItemClick(i) {
      this.$notify({message: `Click ${i}`});
    },
  },
}

</script>

