<template>
  <div id="TabsPage" style="background-color:#eee;">
    <vue-nav-bar
          title=""
          left-text=""
          left-arrow
          @click-left="goBack"
    />
    <div class="div_content">

      <vue-tabs v-model="editableTabsValue" type="card" editable @edit="handleTabsEdit">
        <vue-tab-pane v-for="(item, index) in editableTabs" :key="index" :label="item.title" :name="item.name">
          <div class="div_content_item">
            <div style="margin:6px;">  {{item.content}}  editable </div>
          </div>
        </vue-tab-pane>
      </vue-tabs>
      
      


    </div>
  </div>
</template>

<style scoped>
  .div_content{
    height: calc(100vh - 46px);
  }
  .div_content_item{
    margin:10px 0;
    background-color: #ffffff;
    padding:15px 6px;
    border-radius: 4px;
  }
</style>

<script>
export default {
  name: 'TabsPage',
  components: {
  },

  data() {
    return {
        editableTabsValue: "2",
         editableTabs: [{
           title: "Tab 1",
           name: "1",
           content: "Tab 1 content"
         }, {
           title: "Tab 2",
           name: "2",
           content: "Tab 2 content"
         }],
         tabIndex: 2
      
    };
  },
  methods: {
    goBack() {
        this.$router.go(-1);
    },
    handleTabsEdit(targetName, action) {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const self = this;
         if (action === "add") {
           const newTabName = ++this.tabIndex + "";
           self.editableTabs.push({
             title: "New Long text test---Tab " + newTabName,
             name: newTabName,
             content: "New Long text test---Tab " + newTabName + " content"
           });
           self.editableTabsValue = newTabName;
         }
         if (action === "remove") {
           const tabs = self.editableTabs;
           if (self.editableTabsValue === targetName) {
             tabs.forEach(function(tab, index) {
               if (tab.name === targetName) {
                 const nextTab = tabs[index + 1] || tabs[index - 1];
                 if (nextTab) {
                   self.editableTabsValue = nextTab.name;
                 }
               }
             });
           }
           self.editableTabs = tabs.filter(function(tab) {
             return tab.name !== targetName
           });
         }
    }
  },
};
</script>
