<template>
  <div id="Demo5Page" style="background-color:#eee;overflow:hidden;">
    <vue-nav-bar
          title="TabBar"
          left-text=""
          left-arrow
          @click-left="goBack"
    />
    <div class="md-example-child-scroll-view-5">
      <vue-tabbar
        v-model="activeBlockIndex"
        ref="tabBar"
        :fixed="false"
        @change="onTabChange"
      >
      <vue-tabbar-item v-for="item in tabBarItems" :key="item.index">{{item.label}}</vue-tabbar-item>
      </vue-tabbar>
      <vue-scroll-view
        id="myScrollViewId"
        class="scroll-view-with-tab-bar"
        ref="scrollView"
        :scrolling-x="false"
        @scroll="onScroll"
        @mousedown.native="onScrollStart"
        @touchstart.native="onScrollStart"
      >
        <div
          v-for="i in category"
          :key="i"
          class="scroll-view-category"
        >
          <div
            v-for="j in list"
            :key="j"
            class="scroll-view-item">{{`${i} - ${j}`}}
          </div>
        </div>
      </vue-scroll-view>
    </div>
  </div>
</template>


<style>
.md-example-child-scroll-view-5 {
  position: relative;
  height: calc(100vh - 50px);
  background: #FFF;
}
#myScrollViewId{
  height: calc(100% - 50px);
}
.scroll-view-with-tab-bar.scroll-view-container {
    padding-top: 100px;
  }
  
.scroll-view-item {
   width:100vw;
    height: 42px;
    padding: 10px 20px;
    font-size: 20px;
    border-bottom: .5px solid #efefef;
  }
</style>

<script>

const debounce = function(fn, delay) {
  let timer
  return function() {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const context = this;
    timer && clearTimeout(timer)

    timer = setTimeout(function() {
      // eslint-disable-next-line prefer-rest-params
      fn.apply(context, arguments)
    }, delay)
  }
}
export default {
  name: 'Demo5Page',
  components: {
  },
  data() {
    return {
      category: 6,
      list: 20,
      dimensions: [],
      scrollY: 0,
      isManual: false,
      activeBlockIndex: 0,
    }
  },
  computed: {
    tabBarItems() {
      return this.dimensions.map((item, index) => {
        return {name: index, label: `Tab - ${index + 1}`}
      })
    },
  },
  mounted() {
    // blockscroller
    this.initScrollBlock()
    // this.$refs.scrollView.reflowScroller()
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    initScrollBlock() {
      const blocks = this.$el.querySelectorAll('.scroll-view-category')

      let offset = 0
      Array.prototype.slice.call(blocks).forEach((block, index) => {
        const innerHeight = block.clientHeight
        this.$set(this.dimensions, index, [offset, offset + innerHeight])
        offset += innerHeight
      })

      // setTimeout(() => {
      //   this.$refs.tabBar.reflow()
      // }, 1000)
    },
    onScrollStart() {
      this.isManual = false
    },
    onScroll({scrollTop}) {
      if (!this.isManual) {
        this.dimensions.some((dimension, index) => {
          if (scrollTop >= dimension[0] && scrollTop <= dimension[1]) {
            this.activeBlockIndex = index
            return true
          }
        })
      }
    },
    onTabChange(index) {
      this.isManual = true
      debounce(() => {
        const offsetTop = this.dimensions[index][0];
        this.$refs.scrollView.scrollTo(0, offsetTop, true)
        this.scrollY = offsetTop
      }, 100)()
    },
  },
}

</script>
