import { createNamespace } from '../utils';
import { isDate } from '../utils/validate/date';
import { padZero } from '../utils/format/string';
import { getTrueValue, getMonthEndDay } from './utils';
import { sharedProps, TimePickerMixin } from './shared';

const currentYear = new Date().getFullYear();
const [createComponent] = createNamespace('date-picker');

export default createComponent({
  mixins: [TimePickerMixin],

  props: {
    ...sharedProps,
    type: {
      type: String,
      default: 'datetime',
    },
    minDate: {
      type: Date,
      default: () => new Date(currentYear - 50, 0, 1),
      validator: isDate,
    },
    maxDate: {
      type: Date,
      default: () => new Date(currentYear + 50, 11, 31),
      validator: isDate,
    },
    firstDayOfWeek:{//,: 1~7
      type:Number,
      default:7,
    }
  },

  watch: {
    filter: 'updateInnerValue',
    minDate: 'updateInnerValue',
    maxDate: 'updateInnerValue',

    value(val) {
      
      val = this.formatValue(val);

      if (val.valueOf() !== this.innerValue.valueOf()) {
        this.innerValue = val;
      }
    },
  },

  computed: {
    actualFirstDayOfWeek(){
       return this.firstDayOfWeek%7;
    },
    ranges() {
      //console.log('innerValue===',this.innerValue)
      const {
        maxYear,
        maxDate,
        maxMonth,
        maxHour,
        maxMinute,
        maxSecond,
      } = this.getBoundary('max', this.innerValue);

      const {
        minYear,
        minDate,
        minMonth,
        minHour,
        minMinute,
        minSecond,
      } = this.getBoundary('min', this.innerValue);

      const result = [
        {
          type: 'year',
          range: [minYear, maxYear],
        },
        {
          type: 'month',
          range: [minMonth, maxMonth],
        },
        {
          type: 'day',
          range: [minDate, maxDate],
        },
        {
          type: 'hour',
          range: [minHour, maxHour],
        },
        {
          type: 'minute',
          range: [minMinute, maxMinute],
        },
        {
          type: 'second',
          range: [minSecond, maxSecond],
        },
      ];

      if (this.type === 'year') result.splice(1, 5);
      if (this.type === 'month') result.splice(2, 4);
      if (this.type === 'date'||this.type === 'daterange') result.splice(3, 3);

      if (this.type === 'week'){
        result.splice(2, 4);
        //console.log("weeknum==",this.getWeeks(this.innerValue.getFullYear(),this.innerValue.getMonth()+1));
        result.push({ type: 'week',range:[1,this.getWeeks(this.innerValue.getFullYear(),this.innerValue.getMonth()+1)]});
      } 

      
      return result;
    },
  },

  methods: {
    formatValue(value) {
      /* if(this.type==='week'){
        if (!isDate(value)) {
          return new Date(value);
        }
        return value;
      } */

      if (!isDate(value)) {
        // value = this.minDate;
        value = new Date();
      }

      value = Math.max(value, this.minDate.getTime());
      value = Math.min(value, this.maxDate.getTime());

      return new Date(value);
    },

    getBoundary(type, value) {
      const boundary = this[`${type}Date`];
      const year = boundary.getFullYear();
      let month = 1;
      let date = 1;
      let hour = 0;
      let minute = 0;
      let second = 0;

      if (type === 'max') {
        month = 12;
        date = getMonthEndDay(value.getFullYear(), value.getMonth() + 1);
        hour = 23;
        minute = 59;
        second = 59;
      }

      if (value.getFullYear() === year) {
        month = boundary.getMonth() + 1;
        if (value.getMonth() + 1 === month) {
          date = boundary.getDate();
          if (value.getDate() === date) {
            hour = boundary.getHours();
            if (value.getHours() === hour) {
              minute = boundary.getMinutes();
            }
            if (value.getMinutes() === minute) {
              second = boundary.getSeconds();
            }
          }
        }
      }

      return {
        [`${type}Year`]: year,
        [`${type}Month`]: month,
        [`${type}Date`]: date,
        [`${type}Hour`]: hour,
        [`${type}Minute`]: minute,
        [`${type}Second`]: second,
      };
    },

    updateInnerValue() {
      
      const indexes = this.getPicker().getIndexes();

      const getValue = (index) => {
        const { values } = this.originColumns[index];
        return getTrueValue(values[indexes[index]]);
      };

      const year = getValue(0);
      const month = getValue(1);
      const maxDate = getMonthEndDay(year, month);

      let date;
      if (this.type === 'month') {
        date = 1;
      } else {
        date = getValue(2);
      }

      date = date > maxDate ? maxDate : date;

      let hour = 0;
      let minute = 0;
      let second = 0;

      if (this.type === 'datetime' || this.type === 'datetimerange') {
        hour = getValue(3);
        minute = getValue(4);
        second = getValue(5);
      }


      if(this.type==='week'){
        const weekNum = this.getWeeks(year,month);

        date = this.getFirstDayOfWeek(year,month,date>weekNum?weekNum:date);
      }
      const value = new Date(year, month - 1, date, hour, minute, second);

     

      this.innerValue = this.formatValue(value);
    },

    onChange(picker) {
      this.updateInnerValue();

      this.$nextTick(() => {
        this.$nextTick(() => {
          this.$emit('change', picker);
        });
      });
    },
    updateColumnValue() {
      
      const value = this.innerValue;
      const { formatter,getWeekByDate } = this;
      
      let values;
      values = [
        formatter('year', `${value.getFullYear()}`),
        formatter('month', padZero(value.getMonth() + 1)),
      ];
      if (this.type === 'week') {
        values.push(formatter('week', getWeekByDate(value)));
      }else{
        values.push( formatter('day', padZero(value.getDate())));
  
        if (this.type === 'datetime') {
          
          values.push(
            formatter('hour', padZero(value.getHours())),
            formatter('minute', padZero(value.getMinutes())),
            formatter('second', padZero(value.getSeconds()))
          );
        }
  
        if (this.type === 'month') {
          values = values.slice(0, 2);
        }
      }

       this.$nextTick(() => {
        this.getPicker().setValues(values);
      }); 
    },
    //(firstDayOfWeek)
    getFirstDayOfWeek(year, month,weekday) {
      
      const d = new Date();
      // 
      d.setFullYear(year, month-1, 1);
      let w1 = d.getDay();
       //if (w1 == 0) w1 = 7;
      if (w1 == this.actualFirstDayOfWeek) w1 = 7;
      // 
      d.setFullYear(year, month, 0);
      // 
      let d1;
      if (w1 != 7) d1 = 7 - w1 + 1 + this.actualFirstDayOfWeek;
      else d1 = 1 ;
      const firstDay = d1+(weekday-1)*7;
      return firstDay;
    },
    getWeeks(year, month) {
      
      const d = new Date();
      // 
      d.setFullYear(year, month-1, 1);
      let w1 = d.getDay();
       if (w1 == 0){ 
     // if (w1 == this.actualFirstDayOfWeek){ 
        w1 = 7;
      }
      // 
      d.setFullYear(year, month, 0);
      const dd = d.getDate();
      // 
      let d1;
      //if (w1 != 1){
      if (w1 != this.actualFirstDayOfWeek){
        //  d1 = 7 - w1 + 2;
        d1 = 7 - w1 + 1 + this.actualFirstDayOfWeek;
      }else{
         d1 = 1;
      }
      const weekCount = Math.ceil((dd-d1+1)/7);
      return weekCount;
    },
    getWeekByDate(str){
  
      str = Date.parse(str);
      const date = new Date(str);
      let month = date.getMonth() + 1;
      let year = date.getFullYear();
      let dateN=new Date(str);
      //
      dateN=new Date(dateN.setDate(1));
      const w1=dateN.getDay();
      // 
      let w = date.getDay();//
      //if(w===0&&w1!=0){//
     if(w===this.actualFirstDayOfWeek&&w1!=this.actualFirstDayOfWeek){//firstDayOfWeekfirstDayOfWeek
        w=7;
      }
      let week = Math.ceil((date.getDate() + 6 - w) / 7);
      //if(w1!=0){//
      if(w1!=this.actualFirstDayOfWeek){//firstDayOfWeek
        //week = Math.ceil((date.getDate() + 6 - w) / 7) - 1;
        week = Math.ceil((date.getDate() + 6 - w) / 7)  ;
      }
      if (week === 0) {//0
        month = date.getMonth();
        if(month===0){//
          month=12;
          year=year-1;
        }
        //const dateLast = new Date(date);
        const dayLast = new Date(year, month, 0).getDate();
        const timestamp = new Date(year, month - 1, dayLast);
        w = new Date(timestamp).getDay();//
        //if (w === 0) {
        if(w === this.actualFirstDayOfWeek){
          w = 7;
        }
        week = Math.ceil((timestamp.getDate() + 6 - w) / 7) - 1;
      }
     // week=cNum[week];
      //const time = year+""+month + "" + week + "";
      //console.log(time);
      return week;
    },
  },
  
});
