      },
      dialogVisible:false,
      handleClose: function(done) {
          this.$confirm({
            message: "Are you sure to close this dialog?"
          }).then(function() {
            done();
          }).catch(function() {
            done(false);
          });
        }
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    clickSelect() {
      this.$notify({message: 'click the select button',position:'bottom-center'});
    },
    clickDelete() {
      this.$notify({message: 'click the delete button'});
    },
    clickCollect() {
      this.$notify({message: 'click the collect button'});
    },
    beforeClose({position, instance}) {
      // position 
      // instance  SwipeCell 
      switch (position) {
        case 'left':
          this.$notify({message: 'click the left position'});
          break;
        case 'cell':
          this.$notify({message: 'click the cell position'});
          break;
        case 'outside':
          instance.close();
          break;
        case 'right':
          this.$notify({message: 'click the right position'});
          // this.dialogVisible = true;
          break;
      }
    }
  },
};
